#intro{width:100%;height:100vh;color:white}#intro .mid-line{position:absolute;bottom:0;top:0;left:0;right:0;margin:auto;width:1px;height:100%;background-color:rgba(255,255,255,0.2)}#intro .mid-line.big{bottom:auto;height:50px;width:4px;background-color:#fff}#intro .mid-line.big .circle{box-sizing:border-box;width:40px;height:40px;border:4px solid #fff;border-radius:50%;position:absolute;bottom:-38px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}#intro .col-left,#intro .col-right{display:inline-block;width:50%;vertical-align:top;position:relative;height:100%}#intro .col-left .name{font-size:24px;position:absolute;display:none}#intro .col-left .name.kirgan{bottom:40%;left:45%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}#intro .col-left .name.quentin{bottom:30%;left:57%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}#intro .col-left .name.lovis{bottom:20%;left:38%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}#intro .col-left .name.clement{bottom:10%;left:55%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}#intro .col-right{padding:20px 60px;box-sizing:border-box}#intro .col-right h1{font-size:80px;font-style:italic;color:#3e3e3e;margin:0;text-transform:uppercase}#intro .col-right .text{box-sizing:border-box;font-size:20px;margin:60px auto 0px auto;display:none}#intro .col-right button.discover{box-sizing:border-box;text-transform:uppercase;padding:8px 12px;border:2px solid rgba(255,255,255,0.5);background-color:transparent;color:rgba(255,255,255,0.5);font-size:20px;position:absolute;bottom:75px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);margin:0 auto;outline:none;cursor:pointer;-webkit-transition:background-color 250ms ease-out,color 250ms ease-out;transition:background-color 250ms ease-out,color 250ms ease-out;display:none}#intro .col-right button.discover:hover{color:#fff;border:2px solid #fff}



