#informations{width:100%;height:100vh;color:white;box-sizing:border-box;padding:100px 40px}#informations .title{font-size:40px;color:#3e3e3e;text-transform:uppercase;position:relative;font-weight:bold}#informations .title:after{content:" ";display:block;width:60px;height:7px;position:absolute;left:0;bottom:-10px;background-color:#ffffff}#informations .wrapper{margin-top:60px}#informations .col-left,#informations .col-right{display:inline-block;width:50%;vertical-align:top;position:relative;height:100%;padding:20px 60px;box-sizing:border-box}#informations .col-left .text,#informations .col-right .text{font-size:18px;color:#cdcdcd}#informations .col-left img,#informations .col-right img{margin-left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);margin-bottom:80px}#informations .col-left .line,#informations .col-right .line{position:absolute;bottom:0;top:0;left:0;right:0;margin:auto;width:1px;height:100vh;background-color:rgba(255,255,255,0.2)}#informations .col-right .img{width:300px;height:300px;margin:0 auto;background-url:url("../assets/vr.png");background-repeat:no-repeat;background-size:cover;background-position:center}#informations .col-right .text{margin:0 auto 20px auto}



